import { api } from "./api"

// Tipos
export interface Cliente {
  id: number
  nome: string
  servico: string
  chegada: string
  prioridade: string
  espera?: string
}

export interface Atendimento {
  id: number
  nome: string
  servico: string
  inicio: string
  atendente: string
}

// Cache local
let cacheClientesNaFila: Cliente[] | null = null
let cacheClientesEmAtendimento: Atendimento[] | null = null
let ultimaAtualizacao = 0

// Tempo de expiração do cache em milissegundos (30 segundos)
const CACHE_EXPIRATION = 30 * 1000

class FilaService {
  // Obter clientes na fila
  async getClientesNaFila(): Promise<Cliente[]> {
    const agora = Date.now()

    // Verificar se o cache é válido
    if (cacheClientesNaFila && agora - ultimaAtualizacao < CACHE_EXPIRATION) {
      console.log("Usando cache para clientes na fila")
      return cacheClientesNaFila
    }

    try {
      console.log("Buscando clientes na fila da API")
      const response = await api.get("/fila")
      cacheClientesNaFila = response.data
      ultimaAtualizacao = agora
      return response.data
    } catch (error) {
      console.error("Erro ao buscar clientes na fila:", error)

      // Se houver um erro, mas tivermos dados em cache, retorne-os mesmo expirados
      if (cacheClientesNaFila) {
        console.log("Usando cache expirado para clientes na fila devido a erro")
        return cacheClientesNaFila
      }

      throw error
    }
  }

  // Obter clientes em atendimento
  async getClientesEmAtendimento(): Promise<Atendimento[]> {
    const agora = Date.now()

    // Verificar se o cache é válido
    if (cacheClientesEmAtendimento && agora - ultimaAtualizacao < CACHE_EXPIRATION) {
      console.log("Usando cache para clientes em atendimento")
      return cacheClientesEmAtendimento
    }

    try {
      console.log("Buscando clientes em atendimento da API")
      const response = await api.get("/atendimentos")
      cacheClientesEmAtendimento = response.data
      ultimaAtualizacao = agora
      return response.data
    } catch (error) {
      console.error("Erro ao buscar clientes em atendimento:", error)

      // Se houver um erro, mas tivermos dados em cache, retorne-os mesmo expirados
      if (cacheClientesEmAtendimento) {
        console.log("Usando cache expirado para clientes em atendimento devido a erro")
        return cacheClientesEmAtendimento
      }

      throw error
    }
  }

  // Adicionar cliente à fila
  async adicionarClienteNaFila(cliente: Omit<Cliente, "id" | "chegada">): Promise<Cliente> {
    try {
      console.log("Adicionando cliente à fila:", cliente)
      const response = await api.post("/fila", cliente)

      // Invalidar o cache de clientes na fila
      cacheClientesNaFila = null

      return response.data
    } catch (error) {
      console.error("Erro ao adicionar cliente à fila:", error)
      throw error
    }
  }

  // Iniciar atendimento
  async iniciarAtendimento(clienteId: number): Promise<Atendimento> {
    try {
      console.log(`Iniciando atendimento para cliente ID ${clienteId}`)

      // Verificar se o cliente já está em atendimento para evitar duplicação
      if (cacheClientesEmAtendimento) {
        const clienteJaEmAtendimento = cacheClientesEmAtendimento.find((a) => a.id === clienteId)
        if (clienteJaEmAtendimento) {
          console.log(`Cliente ID ${clienteId} já está em atendimento, retornando dados existentes`)
          return clienteJaEmAtendimento
        }
      }

      const response = await api.post(`/fila/${clienteId}/atender`)

      // Atualizar os caches
      if (cacheClientesNaFila) {
        cacheClientesNaFila = cacheClientesNaFila.filter((c) => c.id !== clienteId)
      }

      if (cacheClientesEmAtendimento) {
        // Verificar se o cliente já existe no cache para evitar duplicação
        if (!cacheClientesEmAtendimento.some((a) => a.id === response.data.id)) {
          cacheClientesEmAtendimento = [...cacheClientesEmAtendimento, response.data]
        }
      } else {
        cacheClientesEmAtendimento = [response.data]
      }

      return response.data
    } catch (error) {
      console.error(`Erro ao iniciar atendimento para cliente ID ${clienteId}:`, error)
      throw error
    }
  }

  // Finalizar atendimento
  async finalizarAtendimento(atendimentoId: number): Promise<void> {
    try {
      console.log(`Finalizando atendimento ID ${atendimentoId}`)

      // Atualizar o cache imediatamente para melhorar a experiência do usuário
      if (cacheClientesEmAtendimento) {
        cacheClientesEmAtendimento = cacheClientesEmAtendimento.filter((a) => a.id !== atendimentoId)
      }

      // Tentar finalizar o atendimento no servidor
      await api.post(`/atendimentos/${atendimentoId}/finalizar`)

      console.log(`Atendimento ID ${atendimentoId} finalizado com sucesso`)
    } catch (error) {
      console.error(`Erro ao finalizar atendimento ID ${atendimentoId}:`, error)

      // Tentar uma segunda abordagem com método DELETE se o POST falhar
      try {
        console.log(`Tentando método alternativo para finalizar atendimento ID ${atendimentoId}`)
        await api.delete(`/atendimentos/${atendimentoId}`)
        console.log(`Atendimento ID ${atendimentoId} finalizado com método alternativo`)
      } catch (secondError) {
        console.error(`Erro ao finalizar atendimento ID ${atendimentoId} com método alternativo:`, secondError)
        // Propagar o erro original
        throw error
      }
    }
  }

  // Remover cliente da fila
  async removerClienteDaFila(clienteId: number): Promise<void> {
    try {
      console.log(`Removendo cliente ID ${clienteId} da fila`)

      // Atualizar o cache imediatamente para melhorar a experiência do usuário
      if (cacheClientesNaFila) {
        cacheClientesNaFila = cacheClientesNaFila.filter((c) => c.id !== clienteId)
      }

      // Tentar remover o cliente no servidor
      await api.delete(`/fila/${clienteId}`)

      console.log(`Cliente ID ${clienteId} removido da fila com sucesso`)
    } catch (error) {
      console.error(`Erro ao remover cliente ID ${clienteId} da fila:`, error)
      throw error
    }
  }

  // Limpar cache
  limparCache(): void {
    console.log("Limpando cache do serviço de fila")
    cacheClientesNaFila = null
    cacheClientesEmAtendimento = null
    ultimaAtualizacao = 0
  }
}

export const filaService = new FilaService()
